import {reactive, set, watch} from "vue"
import {tokenContract, state, web3} from "@/state";
import axios from "axios";

const API_ENDPOINT = process.env.VUE_APP_API_ENDPOINT

export const store = reactive({
    userBalances: {},
    userInvestments: {},
    userBuildings: {},

    buildings: {},
    map: {},
    stats: {},
    params: {},
    status: {}
});

export async function updateStatus() {
    return new Promise((res, rej) =>
        axios.get(API_ENDPOINT+"/v1/state").then((resp) => {
            store.status = resp.data
            res()
        }).catch(rej))
}
export async function updateBalance(address) {
    return new Promise((res, rej) => {
        if (!web3.utils.isAddress(address)) {
            rej()
            return
        }

        tokenContract.methods.balanceOf(address).call().then((balance) => {
            set(store.userBalances, address, balance.toString())
            res()
        }).catch(rej)
    })
}

export async function updateUserProfile(address) {
    return new Promise((res, rej) => {
        if (!web3.utils.isAddress(address)) {
            set(store.userInvestments, address, [])
            set(store.userBuildings, address, [])
            res()
            return
        }

        axios.get(API_ENDPOINT+"/v1/profile/" + address).then((resp) => {
            set(store.userInvestments, address, resp.data.investments)
            set(store.userBuildings, address, resp.data.buildings)
            res()
        }).catch(rej)
    }
        )
}

export async function updateBuilding(square) {
    return new Promise((res, rej) =>
    axios.get(API_ENDPOINT+"/v1/building/" + square)
        .then((resp) => {
            set(store.buildings, square, resp.data)
            res()
        }).catch(rej))
}

export async function updateBuildings() {
    return new Promise((res, rej) =>
        axios.get(API_ENDPOINT+"/v1/buildings").then((resp) => {
            store.buildings = {}
            for (const listKey in resp.data.list) {
                const building = resp.data.list[listKey];
                set(store.buildings, building.square, building)
            }
            res()
        }).catch(rej))
}

export async function updateMap() {
    return new Promise((res, rej) =>
        axios.get(API_ENDPOINT+"/v1/map").then((resp) => {
            store.map = resp.data
            res()
        }).catch(rej))
}

export async function updateStats() {
    return new Promise((res, rej) =>
        axios.get(API_ENDPOINT+"/v1/stats").then((resp) => {
            store.stats = resp.data
            res()
        }).catch(rej))
}

export async function updateParams() {
    return new Promise((res, rej) => {
        axios.get(API_ENDPOINT+"/v1/params").then((resp) => {
            store.params = resp.data
            res()
        }).catch(rej)
    })
}

watch(() => state.account, async () => {
    await updateUserProfile(state.account)
})

