<template>
  <div class="container">
    <div v-if="isWebApp" style="margin-top: 20px;text-align: center;">
      <button class="button" @click="openGame()">{{ $t("Open Game") }}</button>
    </div>
    <div v-else>Logging in...</div>
  </div>
</template>

<style lang="sass" scoped>
</style>

<script>
import axios from "axios";
import {PROVIDER_TG, state} from "@/state";

export default {
  data() {
    return {
      isWebApp: false,
    }
  },
  mounted() {
    if (window.Telegram.WebApp.initData) {
      this.isWebApp = true
    } else {
      this.login()
    }
  },
  methods: {
    openGame() {
      window.Telegram.WebApp.openLink("https://perfect-city-nft.github.io/babylon/#/tg-login/" + this.$route.params.token);
      window.Telegram.WebApp.close();
    },
    login() {
      axios.get(process.env.VUE_APP_TG_WALLET_API + "/authorize?auth_token=" + this.$route.params.token)
        .then(response => {
          console.log(response)
          state.loggedIn = true
          state.account = response.data.address
          state.currentProvider = PROVIDER_TG
          state.telegramWalletToken = response.data.token

          this.$router.push("/")
        })
        .catch(error => {
          this.$swal({
            title: this.$t("Error"),
            text: this.$t("Login error, please try to request a new login link"),
            icon: "error",
            confirmButtonText: "OK",
          })
          console.log(error)
        })
    },
  }
}
</script>