<template>
  <div class="container has-rules">
   <div class="content">
     <div class="info" v-if="building">
       <h1><a href="#" @click.prevent="$router.go(-1)"><font-awesome-icon icon="fa-solid fa-square-caret-left" /></a> {{ building.title ? building.title : "No Title" }}</h1>
       <div class="img" :class="['tower'+(building.square)]"></div>
       <div class="prop">{{ $t('Owner') }}: <a target="_blank" :href="'https://bscscan.com/address/' + building.owner">{{ building.owner.substr(0, 6) }}...{{ building.owner.substr(38, 42) }}</a></div>
       <div class="prop">{{ $t('Level') }}: <span>{{ building.level }}</span></div>
       <div class="prop">{{ $t('Total Earned') }}: <span>{{ state.token.name }} {{ building.total_earned }}</span></div>
       <div class="prop">{{ $t('Last Claim') }}: <span><time-ago v-if="building.last_claim > 0" locale="en" :datetime="building.last_claim * 1000" long></time-ago><span v-else>-</span></span></div>
       <div class="prop">{{ $t('Total claims') }}: <span>{{ building.claims_count }}</span></div>
       <div class="prop">{{ $t('Created space') }}: <span>{{ state.token.name }}</span></div>
       <div class="prop">{{ $t('Created at') }}: <span>{{ building.created_at | niceDate }}</span></div>
       <div v-if="building.mortgage && building.mortgage.payments_left > 0">
         <div class="subtitle">{{ $t('Mortgage')}}</div>
         <div class="prop">{{ $t('Next payment') }}: <span>{{ state.token.name }} {{ building.mortgage.payment | fromWei(2) }} due {{ building.mortgage.next_payment | niceDate }}</span></div>
         <div class="prop">{{ $t('Total Amount') }}: <span>{{ state.token.name }} {{ building.mortgage.amount | fromWei(2) }}</span></div>
         <div class="prop">{{ $t('Confiscated') }}: <span>{{ building.mortgage.confiscated ? $t("yes") : $t("no") }}</span></div>
       </div>
       <div class="description">{{ building.description }}</div>
       <a :href="building.link" target="_blank" class="link">{{ building.link }}</a>

       <div class="invest">
         <h1>{{ $t('Mint space in Tower ({0}% APY)', [store.params.investments_ratio * store.params.max_daily_claims * 365 * 100]) }}:</h1>
         <login-component>
           <div class="amount">
             <input placeholder="0" type="text" v-model="amount"><div>m2</div>
           </div>

           <div class="description" v-if="rate > 0">{{ $t('You will receive ~{0} {1} each time owner claims his rewards', [estimate, state.token.name]) }}</div>

           <check-sufficient-balance-component :amount="toPay" >
             <button @click="invest()" :disabled="loading">{{ $t('Mint space') }}</button>
           </check-sufficient-balance-component>
         </login-component>
       </div>
     </div>
   </div>

    <div class="rules">
      <InvestmentsRules/>
    </div>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/tower-ref.scss'
</style>

<script>
import {state} from "@/state";
import {TimeAgo} from 'vue2-timeago'
import {store, updateBuilding, updateParams} from "@/store";
import InvestmentsRules from "@/components/rules/Investments.vue";
import CheckSufficientBalanceComponent from "@/components/CheckSufficientBalanceComponent.vue";
import LoginComponent from "@/components/LoginComponent.vue";
import web3 from "web3";

export default {
  data() {
    return {
      loading: false,
      amount: "",
      rate: 0,
      referrerAddress: this.$route.params.ref
    }
  },
  mounted() {
    if (web3.utils.isAddress(this.referrerAddress)) {
      state.referrerAddress = this.referrerAddress
    }

    state.title = this.$t('Tower');
    let load;
    load = () => {
      updateBuilding(this.$route.params.id).then(() => {
        state.title = this.building.title ?? this.$t('Tower');
      }).catch(() => {
        setTimeout(load, 1000)
      })
    }

    state.contract.methods.investmentRewardsRatio().call().then((interest) => {
      state.contract.methods.investmentRewardsRatioDenom().call().then((denom) => {
        this.rate = interest / denom
      })
    })

    updateParams()
  },
  methods: {
    invest() {
      this.loading = true
      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.fundTower(this.$route.params.id,  web3.utils.toBN(this.amountF).mul(web3.utils.toBN(1e18))).encodeABI(),
        value: "0x00"
      };

      let check = (txHash) => {
        state.web3.eth.getTransactionReceipt(txHash).then((receipt) => {
          this.$swal('You\'ve invested ' + Math.round([state.abiDecoder.decodeLogs(receipt.logs)[4]][0].events[2].value / 1e18 * 100) / 100 + ' ' + state.token.name)
          this.loading = false
        }).catch(() => {
          setTimeout(() => {
            check(txHash)
          }, 1000)
        })
      }

      state.sendTx(tx)
          .then((txHash) => check(txHash))
          .catch((error) => {
            this.$swal(error)
            console.log(error)
            this.loading = false
          });
    }
  },
  components: {
    LoginComponent,
    CheckSufficientBalanceComponent,
    InvestmentsRules,
    TimeAgo,
  },
  computed: {
    state() {
      return state
    },
    building() {
      return store.buildings[this.$route.params.id] ?? false
    },
    store() {
      return store
    },
    amountF() {
      if (this.amount == "") {
        return "0"
      }

      return this.amount
    },
    estimate() {
      return Math.round(this.amountF * this.rate * 100) / 100
    },
    toPay() {
      return web3.utils.toWei(this.amountF)
    },
  }
}
</script>