<template>
  <login-component>
    <div class="container has-rules">
      <div class="content">
        <div class="info" v-if="building">
          <h1><a href="#" @click.prevent="$router.go(-1)"><font-awesome-icon icon="fa-solid fa-square-caret-left" /></a> {{ building.title }}</h1>
          <div class="img" :class="['tower'+(building.square)]"></div>
          <div class="prop">{{ $t('Level') }}: <span>{{ building.level }}</span></div>
        </div>

        <check-sufficient-balance-component :amount="toPay" />

        <button @click="upgrade()" :disabled="loading || !price">{{ $t('Upgrade for') }} {{ price | fromWei(2) }} {{ stateData.token.name }}</button>
      </div>
      <div class="rules">
        <real-estate/>
      </div>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/upgrade.scss'
</style>

<script>
import {state} from "@/state";
import LoginComponent from "@/components/LoginComponent";
import RealEstate from "@/components/rules/RealEstate";
import {store, updateBuilding} from "@/store";
import CheckSufficientBalanceComponent from "@/components/CheckSufficientBalanceComponent";

export default {
  components: {RealEstate, LoginComponent, CheckSufficientBalanceComponent},
  data() {
    return {
      loading: false,
      price: "0"
    }
  },
  methods: {
    upgrade() {
      this.loading = true
      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.upgradeBuilding(this.$route.params.id).encodeABI(),
        value: "0x00",
      };

      state.sendTx(tx)
          .then((txHash) => {
            state.web3.eth.getTransactionReceipt(txHash).then(() => {
              this.$swal(this.$t('You\'ve upgraded your building'))
              this.building.level++

              this.loading = false
            })
          })
          .catch((error) => {
            this.$swal(error)
            console.error(error)
            this.loading = false
          });
    }
  },
  mounted() {
    state.title = this.$t("Upgrade Tower");

    updateBuilding(this.$route.params.id).then(() => {
      state.title = this.$t("Upgrade") + " " + this.building.title;
    })

    state.contract.methods.getPriceForUpgrade().call().then((result) => {
      this.price = result.toString()
    })
  },
  computed: {
    building() {
      return store.buildings[this.$route.params.id] ?? false
    },
    stateData() {
      return state
    },
    toPay() {
      return this.price
    },
  }
}
</script>