<template>
  <login-component>
    <div class="container">
      <form @submit.prevent="edit">
        <h2><a href="#" @click.prevent="$router.go(-1)"><font-awesome-icon icon="fa-solid fa-square-caret-left" /></a> {{ $t("Main Info") }}</h2>
        <div class="item">
          <input type="text" v-model="form.title" :placeholder="$t('Title')">
        </div>
        <div class="item">
          <input type="text" v-model="form.description" :placeholder="$t('Description')">
        </div>
        <div class="item">
          <input type="text" v-model="form.link" :placeholder="$t('Link')">
        </div>
        <button :disabled="loading">{{ $t('Edit Tower') }}</button>
      </form>

      <form @submit.prevent="editBillboard">
        <h2>{{ $t('Billboard') }} <span v-if="building.level < 5">{{ $t('(only from 5 level)') }}</span></h2>
        <div class="item">
          <input :disabled="loading || building.level < 5" type="text" v-model="billboard" :placeholder="$t('Billboard Url (https://)')">
        </div>
        <button :disabled="loading || building.level < 5">{{ $t('Update billboard') }} <span v-if="building.level < 5">{{ $t('(only from 5 level)') }}</span></button>
      </form>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/edit.scss'
</style>

<script>
import {state} from "@/state";
import LoginComponent from "@/components/LoginComponent";
import {store, updateBuilding} from "@/store";

export default {
  components: {LoginComponent},
  data() {
    return {
      form: {
        title: "",
        description: "",
        link: "",
      },
      billboard: "",
      loading: false,
    }
  },
  methods: {
    edit() {
      this.loading = true
      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.setBuildingInfo(this.$route.params.id, this.form.title, this.form.description, this.form.link).encodeABI(),
        value: "0x00",
      };

      state.sendTx(tx)
          .then((txHash) => {
            state.web3.eth.getTransactionReceipt(txHash).then(() => {
              this.$swal('Ok')
              this.loading = false
            })
          })
          .catch((error) => {
            console.log(error)
            this.loading = false
          });
    },
    editBillboard() {
      this.loading = true
      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.setBuildingBillboard(this.$route.params.id, this.billboard).encodeABI(),
        value: "0x00"
      };

      state.sendTx(tx)
          .then((txHash) => {
            state.web3.eth.getTransactionReceipt(txHash).then(() => {
              this.$swal('Ok')
              this.loading = false
            })
          })
          .catch((error) => {
            console.log(error)
            this.loading = false
          });

    },
    load() {
      updateBuilding(this.$route.params.id).then(() => {
        state.title = this.building.title;
        this.form.title = this.building.title;
        this.form.description = this.building.description;
        this.form.link = this.building.link;
        this.billboard = this.building.billboard;
      }).catch(() => setTimeout(this.load, 1000))
    }
  },
  mounted() {
    state.title = this.$t("Edit Tower");
    this.load()
  },
  computed: {
    building() {
      return store.buildings[this.$route.params.id]
    }
  }
}
</script>