<template>
  <login-component>
    <div class="container has-rules">
      <div class="content">
        <h2>{{ $t("Create Tower") }}</h2>
        <div class="mortgage">
          <div class="checkbox">
            <input v-model="mortgage.on" type="checkbox" id="use-mortgage"> <label for="use-mortgage">{{ $t("Use mortgage") }}</label>
          </div>
          <div v-if="mortgage.on">
            <span class="tag">{{ $t("Mortgage amount (max 70% of price)") }}</span>
            <input type="text" placeholder="0" v-model="mortgage.value">

            <div class="terms">
              <div class="title">{{ $t("Mortgage Terms") }}</div>
              <div class="prop">{{ $t("Building price") }}: <b>{{ price | fromWei(2) }} {{ stateData.token.name }}</b></div>
              <div class="prop">{{ $t("Weekly Payment") }}: <b>{{ mortgageEstimate.payment }} {{ stateData.token.name }}</b></div>
              <div class="prop">{{ $t("First Payment") }}: <b>{{ mortgageEstimate.firstPayment | niceDate }}</b></div>
              <div class="prop">{{ $t("Last Payment") }}: <b>{{ mortgageEstimate.lastPayment | niceDate }}</b></div>
              <div class="prop">{{ $t("Payments count") }}: <b>{{ mortgageEstimate.paymentsCount }}</b></div>
              <div class="prop">{{ $t("Weekly Interest Rate") }}: <b>{{ Math.round(mortgageEstimate.interest * 100) }}%</b></div>
              <br>
              <div class="prop">{{ $t("Tower will be confiscated if you miss payment") }}</div>
            </div>
          </div>
        </div>
        <br>
        <check-sufficient-balance-component :amount="toPay" />

        <div>{{ $t("Create Tower at square №{0} for {1} {2}", [squareId, $options.filters.fromWei(toPay, 2), stateData.token.name]) }}</div>
        <button :class="{mort: mortgage.on}" @click.prevent="create()" :disabled="createLoading">{{ $t("Confirm") }} <span v-if="createLoading">⏳</span></button>
      </div>
      <div class="rules">
        <real-estate/>
      </div>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/create.scss'
</style>

<script>
import {fromWei, state, toBN, toWei} from "@/state";
import LoginComponent from "@/components/LoginComponent";
import RealEstate from "@/components/rules/RealEstate";
import CheckSufficientBalanceComponent from "@/components/CheckSufficientBalanceComponent";
import {store, updateBuilding} from "@/store";

export default {
  components: {
    LoginComponent,
    RealEstate,
    CheckSufficientBalanceComponent,
  },
  data() {
    return {
      createLoading: false,
      price: "0",
      mortgage: {
        on: false,
        value: "0",
        interest: 0,
        paymentsCount: 0,
      }
    }
  },
  mounted() {
    state.contract.methods.getPriceForNextBuilding().call().then((result) => {
      this.price = result.toString()
    })

    state.contract.methods.mortgagePayments().call().then((result) => {
      this.mortgage.paymentsCount = result
    })

    state.contract.methods.mortgageInterest().call().then((interest) => {
      state.contract.methods.mortgageInterestDenom().call().then((denom) => {
        this.mortgage.interest = interest / denom
      })
    })

    state.title = this.$t("Create Tower");
  },
  methods: {
    create() {
      this.createLoading = true

      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.createBuilding(this.squareId, state.account, 0, state.web3.utils.toWei(this.mortgage.value)).encodeABI(),
        value: "0x00",
      };

      state.sendTx(tx)
          .then(() => {
            let checkTower = async () => {
              try {
                await updateBuilding(this.squareId);
              } catch (e) {
                setTimeout(checkTower, 1000)
                return
              }

              if (store.buildings[this.squareId]) {
                this.$router.push("/tower/" + this.squareId)
              } else {
                setTimeout(checkTower, 1000)
              }
            }

            checkTower()
          })
          .catch((error) => {
            this.createLoading = false;
            console.error(error);
            this.$swal({
              title: this.$t("Error"),
              text: error.message,
              icon: "error",
            });
          });
    },
  },
  watch: {
    'mortgage.value': function () {
      const max = Math.floor(fromWei(this.price) / 100.0 * 70) - 1;

      if (this.mortgage.value > max) {
        this.mortgage.value = String(max)
      }
    }
  },
  computed: {
    toPay() {
      if (this.mortgage.value < 1) {
        return this.price
      }

      return toBN(this.price).sub(toBN(toWei(this.mortgage.value))).toString()
    },
    squareId() {
      return this.$route.params.squareId;
    },
    stateData() {
      return state
    },
    mortgageEstimate() {
      return {
        payment: (this.mortgage.value * (1+this.mortgage.interest)) / this.mortgage.paymentsCount,
        firstPayment: Date.now() / 1000 + 84600 * 7,
        lastPayment: Date.now() / 1000 + 84600 * (7 * this.mortgage.paymentsCount),
        paymentsCount: this.mortgage.paymentsCount,
        interest: this.mortgage.interest,
      }
    }
  }
}
</script>