import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueSweetalert2 from 'vue-sweetalert2'
import numeral from 'numeral'
import VueI18n from 'vue-i18n'

import 'sweetalert2/dist/sweetalert2.min.css';

/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faMap, faMoneyBill1, faStar, faComment, faCircleQuestion, faEdit, faShareFromSquare, faPaperPlane } from '@fortawesome/free-regular-svg-icons'
import {
  faArrowUpRightFromSquare,
  faCirclePlus,
  faSackDollar,
  faMoneyBill,
  faCircleInfo,
  faSortDown,
  faSquareCaretLeft,
  faPenToSquare,
  faPersonArrowDownToLine,
  faWallet,
  faHotel,
  faCartShopping,
  faMicrochip,
  faWrench,
  faCheckToSlot,
  faSpinner,
  faComputer,
  faGopuram,
  faGift, faGlobe,
  faMoneyBillTrendUp,
} from '@fortawesome/free-solid-svg-icons'
import {web3} from "@/state";

/* add icons to the library */
library.add(faMap, faMoneyBillTrendUp, faGlobe, faGift, faPersonArrowDownToLine, faGopuram, faSackDollar, faComputer, faCirclePlus, faMoneyBill, faPenToSquare, faSquareCaretLeft, faSortDown, faArrowUpRightFromSquare, faWallet, faMoneyBill1, faHotel, faStar, faComment, faCircleQuestion, faEdit, faShareFromSquare, faPaperPlane, faCartShopping, faMicrochip, faWrench, faCheckToSlot, faSpinner, faCircleInfo)

/* add font awesome icon component */
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(VueSweetalert2)

Vue.config.productionTip = false

Vue.filter("formatNumber", function (value, decimals = 2) {
  return numeral(value).format("0,0[.]" + "0".repeat(decimals)).replaceAll(",", '\u2009');
});

Vue.filter('fromWei', function (value, decimals) {
  let val = web3.utils.fromWei(String(value));

  if (decimals) {
    val = Number(val).toFixed(decimals)
  }

  return val
})

Vue.filter('niceDate', function (value) {
  const options = { year: 'numeric', month: 'short', day: 'numeric' }
  return (new Date(value*1000)).toLocaleDateString('en-US', options)
})

Vue.use(VueI18n)

const messages = {
  en: {},
  ru: require("./i8n/ru.json"),
};

let locale = navigator.languages ? navigator.languages[0] : 'en';
if (locale !== 'ru' && locale !== 'en') {
  locale = 'en';
}

export const i18n = new VueI18n({
  locale,
  messages,
  formatFallbackMessages: true
});

new Vue({
  i18n,
  router,
  render: h => h(App),
}).$mount('#app')
