<template>
  <div class="locale">
    <select v-model="$root.$i18n.locale">
      <option v-for="(lang, i) in langs" :key="`Lang${i}`" :value="lang">
        {{ lang.toUpperCase() }}
      </option>
    </select>
    <font-awesome-icon icon="fa-solid fa-globe" />
  </div>
</template>

<script>
export default {
  name: 'locale-component',
  data () {
    return { langs: ['en', 'ru'] }
  },
  mounted() {
    const params = new Proxy(new URLSearchParams(window.location.hash.substring(window.location.hash.indexOf("?"))), {
      get: (searchParams, prop) => searchParams.get(prop),
    });

    if (params.locale) {
      localStorage.setItem("locale", params.locale)
    }

    const locale = localStorage.getItem("locale")
    if (locale) {
      this.$root.$i18n.locale = locale
    }
  },
  watch: {
    "$root.$i18n.locale": function() {
      localStorage.setItem("locale", this.$root.$i18n.locale)
      // if (window.location.toString().indexOf("locale=") !== -1) {
      //   window.location = window.location.toString().substr(0, window.location.toString().indexOf("locale="));
      // }
      // window.location = window.location + "?locale=" + this.$root.$i18n.locale;
    }
  }
}
</script>