<template>
  <div class="status" v-if="store.status.height > 0 && lastHeight > 0 && (lastHeight - store.status.height) > 5">
    {{ $t('Data is old') }}: {{ lastHeight - store.status.height }} {{ $t('blocks to process') }}
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/status-bar.scss'
</style>

<script>
import {state} from "@/state";
import {store, updateStatus} from "@/store";
import axios from "axios";

export default {
  name: 'StatusComponent',
  data() {
    return {
      lastHeight: 0,
    }
  },
  mounted() {
    setInterval(async () => {
      updateStatus();

      this.lastHeight = await state.web3.eth.getBlockNumber()
      let bscScanHeight = await axios.get("https://api.bscscan.com/api?module=proxy&action=eth_blockNumber")
      this.lastHeight = Math.max(this.lastHeight, parseInt(bscScanHeight.data.result, 16))
    }, 3000)
  },
  computed: {
    store() {
      return store
    }
  }
}

</script>