<template>
  <div>
    <div class="map-container">
      <div class="map">
        <div class="row" v-bind:key="'x:' + x" v-for="(_, x) in 10">
          <div class="point" v-bind:key="'y:' + y" v-for="(_, y) in 10" :set1="square = x * 10 + y + 1" :set2="building = buildings[square]" :set3="has = String(square) in buildings">
            <div v-if="has" @click="$router.push('/tower/' + String(x * 10 + y + 1))" class="building" :class="['tower'+(square)]"></div>
            <div v-if="has" class="field asphalt" @click="$router.push('/tower/' + String(x * 10 + y + 1))"></div>
            <div v-if="!has" class="field grass"></div>
            <div v-if="has" class="level">{{ building.level }}</div>
            <a v-if="has && building.billboard" :href="building.link" target="_blank" class="billboard" :style="{'background-image': 'url('+building.billboard+')'}"></a>
            <div class="info" v-if="has">
              <div class="title" v-if="building.title">{{ building.title }}</div>
              <div class="title" v-if="!building.title">The Tower {{ square }}</div>
              <div class="prop">Owner: <a>{{ building.owner.substr(0, 6) }}...{{ building.owner.substr(38, 42) }}</a></div>
              <div class="prop">Level: <span>{{ building.level }}</span></div>
              <div class="prop">Total Earned: <span>{{ stateData.token.name }} {{ building.total_earned }}</span></div>

              <div v-if="building.description" class="description">{{ building.description }}</div>
              <a v-if="building.link" :href="building.link" class="link">{{ building.link }}</a>

              <div class="open" @click="$router.push('/tower/' + String(x * 10 + y + 1))">{{ $t('Open Tower') }}</div>
            </div>
            <div class="info empty" v-if="!has">
              <div class="open" @click="$router.push('/tower/create/' + String(x * 10 + y + 1))">{{ $t('Create Tower') }}</div>
            </div>
            <div v-if="!has" class="empty"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/home.scss'
</style>

<script>
import { state } from '@/state'
import {store, updateMap} from "@/store";

export default {
  mounted() {
    updateMap()
  },
  methods: {
    openLink(link) {
      window.open(link, '_blank').focus();
    }
  },
  watch: {
    "$route": {
      handler: function () {
        if (this.$route.name === "home") {
          updateMap()
        }
      },
    }
  },
  computed: {
    buildings() {
      return store.map.buildings ?? []
    },
    isLoggedIn() {
      return state.isLoggedIn()
    },
    account() {
      return state.account
    },
    profile() {
      return state.profile
    },
    stateData() {
      return state
    }
  }
}
</script>