<template>
  <div class="container">
    <h1>{{ $t('Buy {0} with BNB', [state.token.name]) }}</h1>
    <login-component>
      <div class="amount">
        <input placeholder="0" @input="debounceInput" type="text" v-model="amount"><div>{{ state.token.name }}</div>
      </div>

      <buy-token-component v-if="toBuy != '0'" :amount="toBuy"/>
    </login-component>

    <h1 style="margin-top: 10px">{{ $t('Other exchanges') }}</h1>
    <ul>
      <li><a target="_blank" :href="'https://pancakeswap.finance/info/tokens/' + state.token.address">PancakeSwap</a></li>
      <li><a target="_blank" href="https://my.honee.app/buy/SHEKEL">Honee App</a></li>
    </ul>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/buy.scss'
</style>

<script>
import {state} from "@/state";
import web3 from "web3";
import LoginComponent from "@/components/LoginComponent";
import BuyTokenComponent from "@/components/BuyTokenComponent.vue";
import _ from 'lodash';

export default {
  components: {BuyTokenComponent, LoginComponent},
  data() {
    return {
      amount: "",
      toBuy: "0",
      loading: false,
    }
  },
  methods: {
    debounceInput: _.debounce(function (e) {
      if (e.target.value === "") {
        this.toBuy = "0"
      } else {
        this.toBuy = web3.utils.toWei(e.target.value)
      }
    }, 1000)
  },
  mounted() {
    state.title = this.$t('Buy SHEKEL')
  },
  computed: {
    state() {
      return state
    }
  }
}
</script>