<template>
  <div class="buy-token" >
    <ul>
      <li><a target="_blank" :href="'https://pancakeswap.finance/info/tokens/' + state.token.address">Купить на PancakeSwap</a></li>
      <li><a target="_blank" href="https://my.honee.app/buy/SHEKEL">Купить в Honee App</a></li>
    </ul>
    <div v-if="false">
      <div>
        <div>{{ $t('Buy error') }}: {{ errorData }}</div>
      </div>
      <button @click="buy" :disabled="loading" v-if="!error" class="button">
        <font-awesome-icon icon="fa-solid fa-spinner" spin v-if="loading" />
        <font-awesome-icon icon="fa-solid fa-cart-shopping" v-else />

        <span v-if="searching" style="margin-left: 10px">{{ $t('Searching for the best price...') }} ({{ ((iteration / maxIterations) * 100.0).toFixed(2) }}%)</span>
        <span v-else style="margin-left: 10px">{{ $t('Buy {0} {1} for {2} BNB', [$options.filters.fromWei(estimatedToken, 4), state.token.name, $options.filters.fromWei(estimatedBNB, 4)  ]) }}</span>
      </button>
    </div>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/buy-token.scss'
</style>

<script>
import {state, toBN, toWei, web3} from "@/state";
import axios from "axios";

const fromToken = "0x" + "e".repeat(40);
const toToken = await state.contract.methods.token().call()
const slippage = "1"

export default {
  name: 'BuyTokenComponent',
  props: [
    "amount"
  ],
  mounted() {
    // this.estimate();
  },
  data() {
    return {
      loading: false,
      error: false,
      searching: false,
      estimatedToken: "",
      errorData: "",
      estimatedBNB: "",
      tx: {},
      iteration: 0,
      maxIterations: 25,
      stop: false,
    }
  },
  watch: {
    amount() {
      if (this.loading) {
        this.stop = true;
      }

      this.estimate();
    }
  },
  methods: {
    async estimate() {
      this.estimatedBNB = "";
      this.estimatedToken = "";
      this.error = false;
      this.loading = true;
      this.searching = true;

      let left = 0.0001;
      let right = 100;

      let i = 0;
      for(;;) {
        if (this.stop) {
          this.estimatedBNB = "";
          this.estimatedToken = "";
          this.error = false;
          this.loading = true;
          this.searching = true;
          this.stop = false;
          return;
        }
        console.log("left", left, "right", right)
        i++;
        this.iteration = i;
        if (i > this.maxIterations) {
          this.error = true;
          this.errorData = this.$t("Unable to buy such amount of coins");
          this.loading = false;
          this.searching = false;
          return;
        }
        try {
          let rA = toWei(((left+right)/2).toFixed(5));
          let url  = "https://api.1inch.io/v5.0/56/swap?toTokenAddress="+toToken+"&fromTokenAddress="+fromToken+"&amount="+rA+"&fromAddress="+this.state.account+"&slippage="+slippage;
          if (state.referrerAddress){
            url += "&referrerAddress="+state.referrerAddress+"&fee=3";
          }
          let res = await axios.get(url + "&disableEstimate=true");
          console.log(res)

          if (
              toBN(res.data.toTokenAmount).gt(toBN(this.amount)) &&
              toBN(res.data.toTokenAmount).sub(toBN(this.amount)).lt(toBN(toWei("10")))
          ) {
            try {
              let res = await axios.get(url);

              this.estimatedBNB = res.data.fromTokenAmount;
              this.estimatedToken = res.data.toTokenAmount;
              this.tx = res.data.tx;
            } catch (e) {
              const re = /Not enough BNB balance. Amount: (.*?)\./;
              const found = e.response.data.description.match(re);
              if (found.length > 0) {
                this.errorData = this.$t("Not enough BNB balance. Needed: ") + web3.utils.fromWei(found[1]) + " BNB";
              } else {
                this.errorData = e.response.data.description;
              }
              this.error = true
            }

            this.loading = false;
            this.searching = false;
            break;
          }

          if (toBN(res.data.toTokenAmount).gt(toBN(this.amount))) {
            right = (left+right)/2;
          } else {
            left = (left+right)/2;
          }
        } catch (e) {
          if (e.response?.data?.description) {
            if (e.response?.data?.description.indexOf("insufficient liquidity") > -1) {
              right = (left+right)/2;
            } else {
              console.error(e)
              this.error = true
              this.errorData = e.response.data.description;
              this.loading = false;
              this.searching = false;
              break;
            }
          } else {
            console.error(e)
            this.error = true
            this.errorData = "Error";
            this.loading = false;
            this.searching = false;
            break;
          }
        }
      }
    },
    async buy() {
      this.loading = true;

      state.sendTx(this.tx)
          .then(() => this.$swal("Done"))
          .catch((error) => this.$swal(error))
          .finally(() => this.loading = false);
    }
  },
  computed: {
    state() {
      return state
    },
  }
}

</script>