<template>
  <div>
    <div class="warning" v-if="shouldBuy">
      <b>{{ $t("Warning") }}:</b> {{ $t("Insufficient {0} balance", [state.token.name]) }}. <br><br>
      <buy-token-component :amount="toBuy"/>
    </div>
    <div v-else>
      <slot/>
    </div>
  </div>
</template>

<script>
import {store, updateBalance} from "@/store";
import {state, toBN} from "@/state";
import BuyTokenComponent from "@/components/BuyTokenComponent";

let interval;

export default {
  components: {
    BuyTokenComponent,
  },
  name: 'CheckSufficientBalanceComponent',
  props: [
    "amount"
  ],
  beforeDestroy() {
    clearInterval(interval);
  },
  mounted() {
    updateBalance(state.account)
    interval = setInterval(() => {
      updateBalance(state.account)
    }, 5000)
  },
  computed: {
    shouldBuy() {
      return toBN(this.amount).gt(toBN(store.userBalances[state.account] ?? "0"))
    },
    toBuy() {
      return toBN(this.amount).sub(toBN(store.userBalances[state.account] ?? "0"))
    },
    state() {
      return state
    }
  }
}

</script>