<template>
  <login-component>
    <div class="container">
      <div class="block">
        <h2>Реферальная программа</h2>

        <p>Получайте <span>3%</span> с покупок ваших рефералов в <span>BNB</span>.</p>

        <p>Ваша реферальная ссылка:</p>

        <input onfocus="this.select();" onmouseup="return false;" type="text" :value="'https://perfect-city-nft.github.io/babylon/#/tower?r='+state.account">

        <p>Награды будут поступать на кошелек <span>{{ state.account }}</span></p>
      </div>

      <div class="block">
        <h2>Реферальная страница для башни</h2>

        <p>Получайте: <br>– <span>3%</span> с покупок ваших рефералов в <span>BNB</span> <br>– <span>10%</span> с наград ваших инвесторов в <span>SHEKEL</span>.</p>

        <label for="tower-select" v-if="buildings.length > 0">Выберите башню</label>
        <div class="select" v-bind:key="'building' + item.square" v-for="item in buildings">
          <div :class="{selected:selectedTower==item.square}" href="#" @click.prevent="selectedTower = item.square">{{ item.title ? item.title : "Tower #" + item.square }}</div>
        </div>

        <div v-if="selectedTower">
          <p>Ваша реферальная ссылка:</p>

          <input onfocus="this.select();" onmouseup="return false;" type="text" :value="'https://perfect-city-nft.github.io/babylon/#/t/'+selectedTower+'/'+state.account">
          <p>Награды будут поступать на кошелек <span>{{ state.account }}</span></p>
        </div>
      </div>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/ref.scss'
</style>

<script>
import {state} from "@/state";
import LoginComponent from "@/components/LoginComponent.vue";
import {store, updateBalance, updateUserProfile} from "@/store";

export default {
  components: {LoginComponent},
  data() {
    return {
      selectedTower: 0
    }
  },
  mounted() {
    updateBalance(state.account)
    updateUserProfile(state.account)
  },
  watch: {
    buildings() {
      this.selectedTower = this.buildings[0].square
    }
  },
  computed: {
    state() {
      return state
    },
    buildings() {
      return store.userBuildings[state.account] ?? []
    },
  }
}
</script>