<template>
  <login-component>
    <div class="container has-rules">
      <div class="content">
        <div class="info" v-if="building">
          <h1><a href="#" @click.prevent="$router.go(-1)"><font-awesome-icon icon="fa-solid fa-square-caret-left" /></a> {{ building.title }}</h1>
          <div class="img" :class="['tower'+(building.square)]"></div>
          <div class="prop">{{ $t('Level') }}: <span>{{ building.level }}</span></div>
          <div class="prop">{{ $t('Total Earned') }}: <span>{{ stateData.token.name }} {{ building.total_earned }}</span></div>
        </div>

        <div class="description">
          {{ $t('You will receive {0} {1} after successful mining', [building.mining_reward, stateData.token.name]) }}

          <br><br>

          {{ $t('Available mining slots for today') }}: {{ building.mining_available }}

          <br><br>

          {{ $t('Mining difficulty') }}: {{ niceDifficulty}}

        </div>
        <button @click="mine()" v-if="!mining && !miningDone && building.mining_available > 0 && difficulty != 0"><font-awesome-icon icon="fa-solid fa-microchip" /> {{ $t('Start Mining') }}</button>
        <button @click="stopMine()" v-if="mining"><font-awesome-icon icon="fa-solid fa-microchip" /> {{ $t('Stop Mining') }}</button>

        <div class="miner" v-if="mining">
          <div>{{ $t('Mining at speed') }} ~{{ speedHuman }}, {{ progress * 100000 }}</div>
          <font-awesome-icon icon="fa-solid fa-spinner" spin />
        </div>

        <div class="miner-done" v-if="miningDone">
          <div>{{ $t('Mining completed in') }} {{ miningTime }} <br> {{ $t('Now you can claim the reward') }}</div>
          <font-awesome-icon icon="fa-solid fa-check-to-slot" />
        </div>

        <button :disabled="!miningDone || claimLoading" @click="claim()"><font-awesome-icon icon="fa-solid fa-check-to-slot" /> {{ $t('Get Reward') }}</button>
      </div>
      <div class="rules">
        <real-estate/>
      </div>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/mine.scss'
</style>

<script>
import {state} from "@/state";
import web3 from "web3";
import LoginComponent from "@/components/LoginComponent";
import RealEstate from "@/components/rules/RealEstate";
import {store, updateBuilding} from "@/store";
import NoSleep from 'nosleep.js';

let noSleep = new NoSleep();

let miner;

export default {
  components: {LoginComponent, RealEstate},
  data() {
    return {
      difficulty: 0,
      startTime: 0,
      endTime: 0,
      mining: false,
      miningDone: false,
      hash: "",
      speed: 0,
      work: 0,
      progress: 0,
      progressArgs: "",
      claimLoading: false
    }
  },
  methods: {
    stopMine() {
      this.mining = false
      miner.terminate()
      miner = new Worker('miner.js');
      noSleep.disable()
    },
    async mine() {
      if (!window.Worker) {
        alert('Web worker not supported')
        return
      }

      await noSleep.enable();

      this.mining = true
      this.startTime = performance.now();

      const difficulty = web3.utils.hexToBytes(this.difficulty)
      const _lastHash = await state.contract.methods.lastWorkHash(this.$route.params.id).call()
      const _square = web3.utils.toHex(this.$route.params.id)
      const _args = web3.utils.hexToBytes(state.web3.eth.abi.encodeParameters(['uint8', 'bytes32'], [_square, _lastHash]));

      if (_args != this.progressArgs) {
        this.progressArgs = _args.toString();
        this.progress = 0;
      }

      miner.postMessage({difficulty: difficulty, args: _args, iteration: this.progress});

      miner.onmessage = (e) => {
        if (e.data.type === "done") {
          this.mining = false;
          this.miningDone = true;
          this.hash = web3.utils.toHex(e.data.hash)
          this.work = e.data.work;
          this.endTime = performance.now()
          miner.terminate()
        } else {
          this.speed = e.data.speed;
          this.progress = e.data.iteration;
        }
      }
    },
    claim() {
      this.claimLoading = true

      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.commitMiningWork(this.$route.params.id, this.work).encodeABI(),
        value: "0x00",
      };

      let check = (txHash) => {
        state.web3.eth.getTransactionReceipt(txHash).then((receipt) => {
          let amount = web3.utils.fromWei(web3.utils.hexToNumberString(receipt.logs[0].data));
          this.$swal('You\'ve received ' + amount + ' ' + state.token.name)
          this.miningDone = false
          this.mining = false
          this.claimLoading = false
          this.building.mining_available--;
          this.progress = 0;
          this.progressArgs = "";
        }).catch(() => {
          setTimeout(() => {
            check(txHash)
          }, 1000)
        })
      }

      state.sendTx(tx)
          .then((txHash) => check(txHash))
          .catch((error) => {
            console.error(error)
            this.claimLoading = false
          });
    }
  },
  beforeDestroy() {
    miner.terminate()
    noSleep.disable()
  },
  mounted() {
    state.title = "Mine Tower";
    updateBuilding(this.$route.params.id)

    miner = new Worker('miner.js');

    state.contract.methods.difficulty().call().then((res) => {
      this.difficulty = res
    })

    let p = localStorage.getItem("progress"+this.$route.params.id)
    if (p && p !== "NaN") {
      this.progress = parseInt(p)
    }

    let pArgs = localStorage.getItem("progressArgs"+this.$route.params.id)
    if (pArgs) {
      this.progressArgs = pArgs
    }
  },
  computed: {
    niceDifficulty() {
      if (!this.difficulty) {
        return 0
      }

      let max = web3.utils.toBN(2).pow(web3.utils.toBN(256))
      let d = max.div(web3.utils.toBN(this.difficulty));

      if (d > 1e9) {
        return Math.round(d / 1e9 * 100) / 100 + " GH";
      }

      if (d > 1e6) {
        return Math.round(d / 1e6 * 100) / 100 + " MH";
      }

      if (d > 1e3) {
        return Math.round(d / 1e3 * 100) / 100 + " KH";
      }

      return 0
    },
    building() {
      return store.buildings[this.$route.params.id] ?? false
    },
    stateData() {
      return state
    },
    speedHuman() {
      let res = "";
      if (this.speed > 1000) {
        res = (Math.round((this.speed / 10)) / 100).toFixed(2) + " kH/s";
      } else {
        res = this.speed + " H/s"
      }

      if (this.speed) {
        let max = web3.utils.toBN(2).pow(web3.utils.toBN(256))
        let d = max.div(web3.utils.toBN(this.difficulty)).div(web3.utils.toBN(Math.round(this.speed).toString(10)));

        res += " (~" + d + " seconds)";
      }

      return res
    },
    miningTime() {
      let s = (this.endTime - this.startTime) / 1000;
      let min = Math.floor(s / 60);
      let sec = Math.round(s % 60);

      return min + "m " + sec + "s";
    }
  },
  watch: {
    progress(after, before) {
      if (after != before) {
        localStorage.setItem("progress"+this.$route.params.id, after)
      }
    },
    progressArgs(after, before) {
      if (after != before) {
        localStorage.setItem("progressArgs"+this.$route.params.id, after)
      }
    }
  }
}
</script>