<template>
  <div class="airdrop" @click="get" v-if="amount > 0 && !claimed && !isTimeout">
    <font-awesome-icon icon="fa-solid fa-spinner" spin v-if="loading" />
    <font-awesome-icon icon="fa-solid fa-gift" v-else />
    {{ $t('Get {0} {1} airdrop', [$options.filters.fromWei(amount, 2), state.token.name]) }}
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/airdrop.scss'
</style>

<script>
import {airdropContract, state} from "@/state";
import web3 from "web3";

export default {
  name: 'AirdropComponent',
  mounted() {
    airdropContract.methods.timeout().call().then((result) => {
      this.timeout = result.toString()
    })
    airdropContract.methods.redeemed(state.account).call().then((result) => {
     this.claimed = result
    })
  },
  data() {
    return {
      loading: false,
      timeout: 0,
      list: require("../../../airdrop/list.json"),
      claimed: true
    }
  },
  methods: {
    get() {
      const proof = merkleProof(this.list, {
        address: state.account.toLowerCase(),
        amount: this.amount
      })

      const tx = {
        from: state.account,
        to: airdropContract._address,
        data: airdropContract.methods.redeem(state.account, proof.path, proof.witnesses, this.amount).encodeABI(),
        value: "0x00"
      };

      state.sendTx(tx)
          .then(() => {
            this.$swal(this.$t("You have received {0} coins", [this.$options.filters.fromWei(this.amount, 2)]))
            this.claimed = true
          })
          .catch((error) => {
            this.$swal("Error")
            console.error(error)
          })
          .finally(() => this.loading = false);
    }
  },
  computed: {
    state() {
      return state
    },
    amount() {
      for (let i in this.list) {
        if (this.list[i].address.toLowerCase() == state.account.toLowerCase()) {
          return this.list[i].amount
        }
      }

      return "0"
    },
    isTimeout() {
      return this.timeout < Math.floor(Date.now() / 1000)
    }
  }
}

function merkleProof(items, recipient) {
  let index = items.findIndex((i) => i.address.toLowerCase() === recipient.address.toLowerCase() && i.amount === recipient.amount);
  if (index === -1) throw ('item not found in items: ' + recipient);

  let path = [];
  let witnesses = [];

  let level = items.map(leafHash);

  while (level.length > 1) {
    // Get proof for this level

    let nextIndex = Math.floor(index / 2);

    if (nextIndex * 2 === index) { // left side
      if (index < level.length - 1) { // only if we're not the last in a level with odd number of nodes
        path.push(0);
        witnesses.push(level[index + 1]);
      }
    } else { // right side
      path.push(1);
      witnesses.push(level[index - 1]);
    }

    index = nextIndex;
    level = hashLevel(level);
  }

  return {
    path: path.reduceRight((a, b) => (a << 1) | b, 0),
    witnesses,
  };
}

function hashLevel(level) {
  let nextLevel = [];

  for (let i = 0; i < level.length; i += 2) {
    if (i === level.length - 1) nextLevel.push(level[i]); // odd number of nodes at this level
    else nextLevel.push(nodeHash(level[i], level[i + 1]));
  }

  return nextLevel;
}

function leafHash(leaf) {
  return web3.utils.keccak256(web3.utils.encodePacked('0x00', {value: leaf.address, type: 'address'}, {value:  leaf.amount, type: 'uint256'}));
}

function nodeHash(left, right) {
  return web3.utils.soliditySha3('0x01', left, right);
}
</script>