<template>
  <div>
    <status-component/>
    <div class="sidenav-overlay" v-if="sidenavOpened" @click="toggleSidenav()"></div>
    <div class="sidenav" :class="{opened: sidenavOpened}">
      <div class="title" @click="$router.push('/tower')">{{ $t('Babylon') }}</div>
      <router-link active-class="active" to="/tower"><font-awesome-icon icon="fa-regular fa-map" /> {{ $t('Map') }}</router-link>
      <router-link active-class="active" to="/wallet"><font-awesome-icon icon="fa-solid fa-wallet" /> {{ $t('My Wallet') }} <div class="notification" v-if="totalClaimable > 0"></div></router-link>
      <router-link active-class="active" to="/buy"><font-awesome-icon icon="fa-solid fa-wallet" /> {{ $t('Buy {0}', [state.token.name]) }}</router-link>
      <router-link active-class="active" to="/ref"><font-awesome-icon icon="fa-solid fa-money-bill-trend-up" /> {{ $t('Referral Program') }}</router-link>
      <router-link active-class="active" to="/top"><font-awesome-icon icon="fa-regular fa-star" /> {{ $t('Top Towers') }}</router-link>
      <router-link active-class="active" to="/status"><font-awesome-icon icon="fa-solid fa-circle-info" /> {{ $t('City Status') }}</router-link>
      <router-link active-class="active" to="/chat"><font-awesome-icon icon="fa-regular fa-comment" /> {{ $t('Chat') }}</router-link>
      <router-link active-class="active" to="/rules"><font-awesome-icon icon="fa-solid fa-circle-info" /> {{ $t('Rules of Babylon') }}</router-link>
      <login-component>
      <airdrop-component />
      </login-component>
      <locale-component/>
      <a class="support" target="_blank" href="https://t.me/towers_support_bot">{{ $t('Support') }} <font-awesome-icon icon="fa-solid fa-arrow-up-right-from-square" /></a>
    </div>
    <div class="header" v-if="$route.name !== 'tg-login'">
      <div>
        <div class="burger" @click="toggleSidenav()">
          <svg viewBox="0 0 100 80">
            <rect fill="white" width="100" height="15"></rect>
            <rect fill="white" y="30" width="100" height="15"></rect>
            <rect fill="white" y="60" width="100" height="15"></rect>
          </svg>
          <div class="notification" v-if="notifications > 0"></div>
        </div>
        <span>{{ title }}</span>
        <div v-if="isLoggedIn" class="account"><router-link to="/wallet">{{ account.substring(0, 6) }}..{{ account.substring(38, 42) }}</router-link></div>
        <div v-if="isLoggedIn" class="logout" @click="logout()"></div>
      </div>
    </div>

    <home-view v-show="$route.name === 'home'" />
    <wallet-view v-show="$route.name === 'wallet'" />
    <top-towers-view v-show="$route.name === 'top'" />
    <chat-view v-if="$route.name !== 'tg-login'" v-show="$route.name === 'chat'" />
    <info-view v-show="$route.name === 'rules'" />
    <status-view v-show="$route.name === 'status'" />
    <referral-view v-show="$route.name === 'ref'" />
    <buy-view v-show="$route.name === 'buy'" />
    <router-view/>
  </div>
</template>

<style lang="sass">
@import '@/assets/reset.scss'
@import '@/assets/global.scss'
</style>

<script>
import { state } from '@/state'
import homeView from "@/views/HomeView";
import walletView from "@/views/WalletView";
import topTowersView from "@/views/TopTowersView";
import chatView from "@/views/ChatView";
import infoView from "@/views/InfoView";
import statusView from "@/views/StatusView";
import loginComponent from "@/components/LoginComponent";
import localeComponent from "@/components/LocaleComponent";
import StatusComponent from "@/components/StatusComponent.vue";
import ReferralView from "@/views/ReferralView.vue";
import BuyView from "@/views/BuyView.vue";
import AirdropComponent from "@/components/AirdropComponent.vue";

export default {
  data() {
    return {
      sidenavOpened: false,
      notifications: 0,
    }
  },
  methods: {
    logout() {
      state.logout()
    },
    toggleSidenav() {
      this.sidenavOpened = !this.sidenavOpened
    }
  },
  computed: {
    state() {
      return state
    },
    isLoggedIn() {
      return state.isLoggedIn()
    },
    account() {
      return state.account
    },
    loader() {
      return state.globalLoader > 0
    },
    title() {
      return state.title
    },
    totalClaimable() {
      return state.totalClaimable
    }
  },
  watch: {
    '$route'() {
      state.title = ""
      this.sidenavOpened = false
    },
    title() {
      if (state.title) {
        document.title = state.title
      } else {
        document.title = this.$t("Babylon")
      }
    },
    totalClaimable() {
      if (this.totalClaimable > 0) {
        this.notifications = 1
      } else {
        this.notifications = 0
      }
    }
  },
  components: {
    AirdropComponent,
    BuyView,
    ReferralView,
    StatusComponent,
    homeView,
    walletView,
    topTowersView,
    chatView,
    infoView,
    loginComponent,
    localeComponent,
    statusView,
  }
}
</script>
