<template>
  <div class="container">
    <div class="block">
      <p>{{ $t('Current coin status') }}:</p>
      <ul>
        <li>{{ $t('Coin') }}: <b>{{ state.token.name }}</b></li>
        <li>{{ $t('Price') }}: <b>${{ store.stats.initial_price | formatNumber(5) }} &rarr; ${{ store.stats.current_price | formatNumber(5) }}</b> <span style="color: #00de00">+{{ Math.round((store.stats.current_price - store.stats.initial_price) / store.stats.initial_price * 100) | formatNumber }}%</span></li>
        <li>{{ $t('Emission')}}: <b>{{ store.stats.initial_emission | formatNumber }} &rarr; {{ store.stats.current_emission | formatNumber }} {{ state.token.name }}</b></li>
        <li>{{ $t('Capitalization') }}: <b>${{ store.stats.current_price * store.stats.current_emission | formatNumber }}</b></li>
      </ul>
      <br>

      <p>{{ $t('Current mortgage params') }}:</p>
      <ul>
        <li>{{ $t('Mortgage Payments') }}: <b>{{ store.params.mortgage_payments }}</b></li>
        <li>{{ $t('Mortgage Interest') }}: <b>{{ store.params.mortgage_interest * 100 }}%</b></li>
      </ul>
      <br>

      <p>{{ $t('Current rewards params') }}:</p>
      <ul>
        <li>{{ $t('Base Reward') }}: <b>{{ store.params.tower_base_reward }} {{ state.token.name }} {{ $t('per claim') }}</b></li>
        <li>{{ $t('Delta Reward') }}: <b>{{ store.params.tower_delta_reward }} {{ state.token.name }}</b></li>
        <li>{{ $t('Investments Ratio') }}: <b>{{ store.params.investments_ratio * 100 }}% {{ $t('per claim') }}, {{ store.params.investments_ratio * store.params.max_daily_claims * 365 * 100 }}%
          {{ $t('per year') }}</b></li>
        <li>{{ $t('Tower Owner\'s Commission') }}: <b>{{ store.params.tower_owners_commission * 100 }}%</b></li>
        <li>{{ $t('Max Daily Claims') }}: <b>{{ store.params.max_daily_claims }}</b></li>
        <li>{{ $t('Mining Difficulty') }}: <b>{{ niceDifficulty }}</b></li>
      </ul>
      <br>

      <p>{{ $t('Current towers params') }}:</p>
      <ul>
        <li>{{ $t('Next Tower Price') }}: <b>{{ nextBuildingPrice | fromWei(2) }} {{ state.token.name }}</b></li>
        <li>{{ $t('Towers') }}: <b>{{ store.stats.current_towers }} {{ $t('of') }} {{  store.stats.max_towers }}</b></li>
      </ul>
    </div>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/status.scss'
</style>

<script>
import {store, updateStats, updateParams} from "@/store";
import {state} from "@/state";
import web3 from "web3";

export default {
  mounted() {
    updateStats()
    updateParams()

    state.contract.methods.difficulty().call().then((res) => {
      this.difficulty = res
    })

    state.contract.methods.getPriceForNextBuilding().call().then((result) => {
      this.nextBuildingPrice = result.toString()
    })
  },
  data() {
    return {
      difficulty: 0,
      nextBuildingPrice: 0,
    }
  },
  computed: {
    store() {
      return store
    },
    state() {
      return state
    },
    niceDifficulty() {
      if (!this.difficulty) {
        return 0
      }

      let max = web3.utils.toBN(2).pow(web3.utils.toBN(256))
      let d = max.div(web3.utils.toBN(this.difficulty));

      if (d > 1e9) {
        return Math.round(d / 1e9 * 100) / 100 + " GH";
      }

      if (d > 1e6) {
        return Math.round(d / 1e6 * 100) / 100 + " MH";
      }

      if (d > 1e3) {
        return Math.round(d / 1e3 * 100) / 100 + " KH";
      }

      return 0
    },
  }
}
</script>