<template>
  <div class="container">
    <div class="info" v-if="building">
      <h1><a href="#" @click.prevent="$router.go(-1)"><font-awesome-icon icon="fa-solid fa-square-caret-left" /></a> {{ building.title ? building.title : "No Title" }}</h1>
      <div class="img" :class="['tower'+(building.square)]"></div>
      <div class="prop">{{ $t('Owner') }}: <a target="_blank" :href="'https://bscscan.com/address/' + building.owner">{{ building.owner.substr(0, 6) }}...{{ building.owner.substr(38, 42) }}</a></div>
      <div class="prop">{{ $t('Level') }}: <span>{{ building.level }}</span></div>
      <div class="prop">{{ $t('Total Earned') }}: <span>{{ state.token.name }} {{ building.total_earned }}</span></div>
      <div class="prop">{{ $t('Last Claim') }}: <span><time-ago v-if="building.last_claim > 0" locale="en" :datetime="building.last_claim * 1000" long></time-ago><span v-else>-</span></span></div>
      <div class="prop">{{ $t('Mining Reward') }}: <span>{{ state.token.name }} {{ building.mining_reward }}</span></div>
      <div class="prop">{{ $t('Mining available for today') }}: <span>{{ building.mining_available }}</span></div>
      <div class="prop">{{ $t('Total claims') }}: <span>{{ building.claims_count }}</span></div>
      <div class="prop">{{ $t('Created space') }}: <span>{{ building.invested_amount }}</span></div>
      <div class="prop">{{ $t('Space owners') }}: <span>{{ building.investments_count }}</span></div>
      <div class="prop">{{ $t('Created at') }}: <span>{{ building.created_at | niceDate }}</span></div>
      <div v-if="building.mortgage && building.mortgage.payments_left > 0">
        <div class="subtitle">{{ $t('Mortgage')}}</div>
        <div class="prop">{{ $t('Next payment') }}: <span>{{ state.token.name }} {{ building.mortgage.payment | fromWei(2) }} due {{ building.mortgage.next_payment | niceDate }}</span></div>
        <div class="prop">{{ $t('Total Amount') }}: <span>{{ state.token.name }} {{ building.mortgage.amount | fromWei(2) }}</span></div>
        <div class="prop">{{ $t('Payments left') }}: <span>{{ building.mortgage.payments_left }}</span></div>
        <div class="prop">{{ $t('Confiscated') }}: <span>{{ building.mortgage.confiscated ? $t("yes") : $t("no") }}</span></div>
      </div>
      <div class="description">{{ building.description }}</div>
      <a :href="building.link" target="_blank" class="link">{{ building.link }}</a>

      <div class="buttons">
        <button @click="$router.push('/tower/invest/' + building.square)"><font-awesome-icon icon="fa-solid fa-cart-shopping" /> {{ $t('Mint space') }}</button>
        <button :disabled="building.owner.toLowerCase() !== state.account.toLowerCase()" @click="$router.push('/tower/mine/' + building.square)"><font-awesome-icon icon="fa-solid fa-microchip" /> {{ $t('Mine') }}</button>
        <button :disabled="building.owner.toLowerCase() !== state.account.toLowerCase()" @click="$router.push('/tower/upgrade/' + building.square)"><font-awesome-icon icon="fa-solid fa-wrench" /> {{ $t('Upgrade') }}</button>
        <button :disabled="building.owner.toLowerCase() !== state.account.toLowerCase()" @click="$router.push('/tower/edit/' + building.square)"><font-awesome-icon icon="fa-solid fa-pen-to-square" /> {{ $t('Edit') }}</button>
      </div>
    </div>
    <div class="comments">
      <component v-bind:is="'script'" src="https://telegram.org/js/telegram-widget.js?19" :data-telegram-discussion="'babylon_nft_city/' + (82 + Number(this.$route.params.id))" data-comments-limit="5" data-dark="1"></component>
    </div>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/tower.scss'
</style>

<script>
import {state} from "@/state";
import {TimeAgo} from 'vue2-timeago'
import {store, updateBuilding} from "@/store";

export default {
  mounted() {
    state.title = this.$t('Tower');
    let load;
    load = () => {
      updateBuilding(this.$route.params.id).then(() => {
        state.title = this.building.title ?? this.$t('Tower');
      }).catch(() => {
        setTimeout(load, 1000)
      })
    }
  },
  components: {
    TimeAgo,
  },
  computed: {
    state() {
      return state
    },
    building() {
      return store.buildings[this.$route.params.id] ?? false
    }
  }
}
</script>