<template>
  <login-component>
    <div class="container has-rules">
      <div class="content">
        <div class="info" v-if="building">
          <h1><a href="#" @click.prevent="$router.go(-1)"><font-awesome-icon icon="fa-solid fa-square-caret-left" /></a> {{ building.title }}</h1>
          <div class="img" :class="['tower'+(building.square)]"></div>
          <div class="prop">{{ $t('Level') }}: <span>{{ building.level }}</span></div>
          <div class="prop">{{ $t('Total Earned') }}: <span>{{ stateData.token.name }} {{ building.total_earned }}</span></div>
        </div>

        <span class="title">{{ $t('Amount') }}</span>
        <div class="amount">
          <input placeholder="0" type="text" v-model="amount"><div>m2</div>
        </div>

        <div class="description" v-if="rate > 0">{{ $t('You will receive ~{0} {1} each time owner claims his rewards', [estimate, stateData.token.name]) }}</div>

        <check-sufficient-balance-component :amount="toPay" />

        <div class="warning" v-if="hasUnclaimedRewards">
          <b>{{ $t("Warning") }}:</b> {{ $t("You have unclaimed rewards. You should claim them before minting new spaces") }}. <br><br>

          <button @click.prevent="$router.push('/wallet')" class="button">
              <span>{{ $t("Go to wallet") }}</span>
          </button>
        </div>

        <button @click="invest()" :disabled="loading || hasUnclaimedRewards">{{ $t('Mint space') }}</button>
      </div>
      <div class="rules">
        <investments-rules/>
      </div>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/invest.scss'
</style>

<script>
import {state} from "@/state";
import web3 from "web3";
import LoginComponent from "@/components/LoginComponent";
import InvestmentsRules from "@/components/rules/Investments"
import {store, updateBuilding, updateUserProfile} from "@/store";
import CheckSufficientBalanceComponent from "@/components/CheckSufficientBalanceComponent";

export default {
  components: {LoginComponent, InvestmentsRules, CheckSufficientBalanceComponent},
  data() {
    return {
      amount: "0",
      loading: false,
      rate: 0
    }
  },
  methods: {
    invest() {
      this.loading = true
      const tx = {
        from: state.account,
        to: state.contract._address,
        data: state.contract.methods.fundTower(this.$route.params.id, web3.utils.toWei(String(this.amount))).encodeABI(),
        value: "0x00"
      };

      let check = (txHash) => {
        state.web3.eth.getTransactionReceipt(txHash).then((receipt) => {
          this.$swal('You\'ve minted ' + Math.round([state.abiDecoder.decodeLogs(receipt.logs)[4]][0].events[2].value / 1e18 * 100) / 100 + 'm2')
          this.loading = false
        }).catch(() => {
          setTimeout(() => {
            check(txHash)
          }, 1000)
        })
      }

      state.sendTx(tx)
          .then((txHash) => check(txHash))
          .catch((error) => {
            this.$swal(error)
            console.log(error)
            this.loading = false
          });
    }
  },
  mounted() {
    state.title = this.$t('Mint a space in Tower')
    updateBuilding(this.$route.params.id)
    updateUserProfile(state.account)

    state.contract.methods.investmentRewardsRatio().call().then((interest) => {
      state.contract.methods.investmentRewardsRatioDenom().call().then((denom) => {
        this.rate = interest / denom
      })
    })
  },
  computed: {
    investments() {
      return store.userInvestments[state.account] ?? []
    },
    hasUnclaimedRewards() {
      return this.investments.reduce((sum, item) => {
        if (item.building.square == this.$route.params.id) {
          return sum + item.to_claim / 1e18;
        }

        return sum;
      }, 0) > 0
    },
    building() {
      return store.buildings[this.$route.params.id]
    },
    estimate() {
      return Math.round(this.amount * this.rate * 100) / 100
    },
    stateData() {
      return state
    },
    toPay() {
      return web3.utils.toWei(this.amount)
    },
  }
}
</script>