<template>
  <div class="container has-rules">
    <div class="content">
      <div class="list" :i="i = 0">
        <div class="item" :i="i++" v-bind:key="'top' + building.square" v-for="building in buildings">
          <div class="img" :class="['tower'+(building.square)]"></div>
          <div class="data" @click="$router.push('/tower/' + building.square)">
            <div class="title">{{ i }}. {{ building.title ? building.title : "Tower #" + building.square }}</div>
            <div class="prop">{{ $t('Level') }}: <span>{{ building.level }}</span></div>
            <div class="prop">{{ $t('Total Earned') }}: <span>{{ stateData.token.name }} {{ building.total_earned }}</span></div>
            <div class="prop">{{ $t('Last Claim') }}: <span v-if="building.last_claim"><time-ago locale="en" :datetime="building.last_claim * 1000" long></time-ago></span><span v-else>-</span></div>
            <div class="prop">{{ $t('Invested') }}: <span>{{ stateData.token.name }} {{ building.invested_amount }}</span></div>

            <div class="description">{{ building.description }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="rules">
      <real-estate/>
    </div>
  </div>
</template>

<style lang="sass" scoped>
@import '@/assets/towers.scss'
</style>

<script>
import {state} from "@/state";

import { TimeAgo } from 'vue2-timeago'
import RealEstate from "@/components/rules/RealEstate";
import {store, updateBuildings} from "@/store";

export default {
  mounted() {
    updateBuildings()
  },
  computed: {
    buildings() {
      if (!store.buildings) {
        return [];
      }

      let list = [];

      for (let i in store.buildings) {
        let building = store.buildings[i];
        building.square = i;
        list.push(building);
      }

      return list.sort((a, b) => {
        return b.invested_amount - a.invested_amount;
      });
    },
    stateData() {
      return state
    }
  },
  components: {
    TimeAgo,
    RealEstate
  }
}
</script>