import Vue from 'vue'

import VueRouter from 'vue-router'
import TowerView from '@/views/TowerView'
import CreateTowerView from '@/views/CreateTowerView'
import EditTowerView from '@/views/EditTowerView'
import InvestView from '@/views/InvestView'
import UpgradeView from '@/views/UpgradeView'
import MineView from "@/views/MineView";
import TowerRefView from "@/views/TowerRefView.vue";
import web3 from "web3";
import {state} from "@/state";
import TgLoginView from "@/views/TgLoginView.vue";

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/tower' },
  {
    path: "/tower",
    name: "home",
  },
  {
    path: "/ref",
    name: "ref",
  },
  {
    path: "/tower/:id",
    name: "tower",
    component: TowerView,
  },
  {
    path: "/t/:id/:ref",
    name: "tower-ref",
    component: TowerRefView,
  },
  {
    path: "/tower/create/:squareId",
    name: "createTower",
    component: CreateTowerView,
  },
  {
    path: "/tower/edit/:id",
    name: "editTower",
    component: EditTowerView,
  },
  {
    path: "/tower/invest/:id",
    name: "invest",
    component: InvestView,
  },
  {
    path: "/tower/upgrade/:id",
    name: "upgrade",
    component: UpgradeView,
  },
  {
    path: "/tower/mine/:id",
    name: "mine",
    component: MineView,
  },
  {
    path: "/wallet",
    name: "wallet",
  },
  {
    path: "/chat",
    name: "chat",
  },
  {
    path: "/top",
    name: "top",
  },
  {
    path: "/rules",
    name: "rules",
  },
  {
    path: "/status",
    name: "status",
  },
  {
    path: "/buy",
    name: "buy",
  },
  {
    path: "/tg-login/:token",
    name: "tg-login",
    component: TgLoginView,
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.query.r && web3.utils.isAddress(to.query.r)) {
    state.referrerAddress = to.query.r;
  }
  next()
})

export default router
