<template>
  <login-component>
    <div class="container">
      <div class="wallet">
        <h1>{{ $t('Your Wallet') }}</h1>
        <a class="address" target="_blank" :href="'https://bscscan.com/address/' + account">{{ account }}</a>
        <div class="balance">{{ $t("Balance") }}: {{ balance | fromWei | formatNumber }} {{ stateData.token.name }}</div>

        <a target="_blank" class="button" @click.prevent="$router.push('/buy')">{{ $t('Trade') }} {{ stateData.token.name }}</a>

        <button class="button" @click="logout">{{ $t('Logout') }}</button>
      </div>

      <div class="investments">
        <div class="mortgages" v-if="mortgages.length > 0">
          <h1>{{ $t('Mortgages') }}</h1>

          <div class="mortgage" v-bind:key="'mortgage' + building.mortgage.ID"  v-for="building in mortgages">
            <div class="title">{{ $t('Mortgage') }} #{{ building.mortgage.ID }} for <a href="#" @click.prevent="$router.push('/tower/' + building.square)">{{ $t('Tower') }} #{{ building.square }}</a></div>
            <div class="prop">{{ $t('Next payment') }}: <span>{{ stateData.token.name }} {{ building.mortgage.payment | fromWei(2) }}</span> due <span>{{ building.mortgage.next_payment | niceDate }}</span></div>
            <div class="prop">{{ $t('Total Amount') }}: <span>{{ stateData.token.name }} {{ building.mortgage.amount  | fromWei(2) }}</span></div>
            <div class="prop">{{ $t('Payments left') }}: <span>{{ building.mortgage.payments_left }}</span></div>

            <button v-if="building.mortgage.payments_left > 0" class="button" @click="payMortgage(building.mortgage.ID)">{{ $t('Make payment of {0} {1}', [stateData.token.name, $options.filters.fromWei(building.mortgage.payment, 2)]) }}</button>
          </div>
        </div>

        <h1>{{ $t('Your Spaces') }}</h1>
        <div class="actions">
          <div @click="claimRewards()" :class="{disabled: loading || totalClaimable == 0}">
            <font-awesome-icon icon="fa-solid fa-person-arrow-down-to-line" />
            {{ $t('Collect All Rewards') }} ({{ totalClaimable.toFixed(5) }} {{ stateData.token.name }})
          </div>
        </div>
        <div class="item" v-bind:key="'investment' + item.id" v-for="item in investments">
          <div class="image" :class="['tower'+(item.building.square)]"></div>
          <div class="info">
            <div class="title" @click="$router.push('/tower/' + item.building.square)">{{ item.building.title ? item.building.title : "Tower #" + item.building.square }}</div>
            <div class="param">{{ $t('Owned') }}: <span>{{ item.amount | fromWei(2) }} m2</span></div>
            <div class="param">{{ $t('Total Revenue') }}: <span>{{ stateData.token.name }} {{ item.total_earned.toFixed(5) }}</span></div>
          </div>
          <div class="right">
          <span v-if="item.to_claim > 0">
            +{{ item.to_claim | fromWei }} {{ stateData.token.name }}
          </span>
          </div>
        </div>
      </div>

      <div class="investments">
        <h1>{{ $t('Your Towers') }}</h1>
        <div class="item" v-bind:key="'building' + item.square" v-for="item in buildings">
          <div class="image" :class="['tower'+(item.square)]"></div>
          <div class="info">
            <div class="title" @click="$router.push('/tower/' + item.square)">{{ item.title ? item.title : "Tower #" + item.square }}</div>
            <div class="param">{{ $t('Mines available') }}: <span>{{ item.mining_available }}</span></div>
            <div class="param">{{ $t('1 mine profit') }}: <span>{{ stateData.token.name }} {{ item.mining_reward }}</span></div>
          </div>
          <div class="right">
          <span>
           <button :disabled="item.mining_available === 0" @click.prevent="$router.push('/tower/mine/' + item.square)"><font-awesome-icon icon="fa-solid fa-microchip" /> {{ $t('Mine') }}</button>
          </span>
          </div>
        </div>
      </div>
    </div>
  </login-component>
</template>

<style lang="sass" scoped>
@import '@/assets/wallet.scss'
</style>

<script>
import {state} from "@/state";
import LoginComponent from "@/components/LoginComponent";
import {store, updateBalance, updateUserProfile} from "@/store";

export default {
  components: {LoginComponent},
  data() {
    return {
      loading: false
    }
  },
  mounted() {
    let tryUpdateBalance = () => {
      if (this.account) {
        this.updateBalance()
        setInterval(this.updateBalance, 5000)
      } else {
        setTimeout(tryUpdateBalance, 500)
      }
    }

    tryUpdateBalance()
  },
  watch: {
    totalClaimable() {
      state.totalClaimable = this.totalClaimable
    }
  },
  computed: {
    account() {
      return state.account
    },
    balance() {
      return store.userBalances[this.account] ?? "0"
    },
    investments() {
      return store.userInvestments[this.account] ?? []
    },
    buildings() {
      return store.userBuildings[this.account] ?? []
    },
    mortgages() {
      return this.buildings.filter(function (item) {
        return item.mortgage
      })
    },
    totalClaimable() {
      if (!this.investments) {
        return 0
      }

      return this.investments.reduce(function (sum, item) {
        return sum + item.to_claim / 1e18;
      }, 0);
    },
    stateData() {
      return state
    }
  },
  methods: {
    logout() {
      state.logout()
    },
    updateBalance() {
      updateBalance(this.account)
      updateUserProfile(this.account)
    },
    payMortgage(id) {
      try {
        this.loading = true
        const tx = {
          from: this.account,
          to: state.contract._address,
          data: state.contract.methods.payMortgage(id).encodeABI(),
          value: "0x00",
        };

        state.sendTx(tx)
            .then((txHash) => {
              state.web3.eth.getTransactionReceipt(txHash).then(() => {
                this.$swal(this.$t('You\'ve made your payment'))
                this.loading = false
              })
            })
            .catch((e) => {
              this.$swal(this.$t('Error'), e, 'error')
              this.loading = false
            });
      } catch (e) {
        this.$swal(this.$t('Error'), e, 'error')
        this.loading = false
      }
    },
    claimRewards() {
      if (this.totalClaimable == 0) {
        return;
      }

      this.loading = true

      let ids = this.investments.map(function (item) {
        if (item.to_claim > 0) {
          return item.building.square;
        }

        return false;
      }).filter((v, i, a) => v != false && a.indexOf(v) === i);

      const tx = {
        from: this.account,
        to: state.contract._address,
        data: state.contract.methods.claimRewards(ids).encodeABI(),
        value: "0x00"
      };

      state.sendTx(tx)
          .then((txHash) => {
            state.web3.eth.getTransactionReceipt(txHash).then(() => {
              this.$swal(this.$t('You\'ve claimed your rewards'))
              this.loading = false
            })
          })
          .catch((error) => {
            console.error(error)
            this.loading = false
          });
    }
  }
}
</script>