<template>
  <div>
    <div v-if="!isLoggedIn">
      <button class="login-button telegram" @click.prevent="loginTelegram">Telegram</button>
      <button class="login-button metamask" v-if="metamaskAvailable" @click.prevent="loginMetamask">Metamask</button>
      <button class="login-button walletconnect" @click.prevent="loginWalletConnect">WalletConnect</button>
    </div>
    <slot v-else />
  </div>
</template>

<script>
import {PROVIDER_MM, PROVIDER_TG, PROVIDER_WC, state} from "@/state";

export default {
  name: 'LoginComponent',
  data() {
    return {
      metamaskAvailable: false,
    }
  },
  mounted() {
    if (typeof window.ethereum !== 'undefined') {
      this.metamaskAvailable = true
    }
  },
  methods: {
    loginMetamask() {
      state.login(PROVIDER_MM)
    },
    loginWalletConnect() {
      state.login(PROVIDER_WC)
    },
    loginTelegram() {
      state.login(PROVIDER_TG)
    },
  },
  computed: {
    isLoggedIn() {
      return state.isLoggedIn()
    },
  }
}
</script>