import Vue, {reactive, watch} from "vue"
import WalletConnect from "@walletconnect/client"
import QRCodeModal from "@walletconnect/qrcode-modal"
import Web3 from "web3";
import abiDecoder from 'abi-decoder';
import axios from "axios";
import {i18n} from "@/main";

const towersCityABI = require("./abi/TheTowersCity.json");
const tokenABI = require("./abi/Token.json");
const airdropABI = require("./abi/Airdrop.json");

export const PROVIDER_WC = "walletconnect";
export const PROVIDER_MM = "metamask";
export const PROVIDER_TG = "telegram";

abiDecoder.addABI(towersCityABI)
abiDecoder.addABI(tokenABI)

// Create a connector
const walletConnect = new WalletConnect({
    bridge: "https://bridge.walletconnect.org", // Required
    qrcodeModal: QRCodeModal,
});

export const web3 = new Web3(process.env.VUE_APP_EVM_NODE);
export const toBN = web3.utils.toBN
export const toWei = web3.utils.toWei
export const fromWei = web3.utils.fromWei

let Contract = require('web3-eth-contract');
Contract.setProvider(process.env.VUE_APP_EVM_NODE);

let contract = new Contract(towersCityABI, process.env.VUE_APP_TOWERS_CITY_CONTRACT);

export let airdropContract = new web3.eth.Contract(airdropABI, "0xfaa46D64f71987895F11632C18a955d2cB062b0f");

export const state = reactive({
    abiDecoder: abiDecoder,
    web3: web3,
    contract: contract,
    loggedIn: false,
    account: "",
    telegramWalletToken: "",
    referrerAddress: "",
    profile: {},
    globalLoader: 0,
    title: "",
    token: {
        name: "SHEKEL",
        address: "",
    },
    currentProvider: "",
    sendTx: async function (tx) {
        // todo: estimate gas
        // await web3.eth.estimateGas(tx);

        switch (this.currentProvider) {
            case PROVIDER_WC:
                return walletConnect.sendTransaction(tx)
            case PROVIDER_MM:
                return window.ethereum.request({
                    method: 'eth_sendTransaction',
                    params: [tx],
                })
            case PROVIDER_TG: {
                let result = await axios.get(process.env.VUE_APP_TG_WALLET_API + "/submit_tx?token=" + this.telegramWalletToken + "&tx=" + JSON.stringify(tx));
                console.log(result);

                return new Promise((resolve, reject) => {
                    let popup = Vue.swal({
                        title: i18n.t("Transaction submitted"),
                        text: i18n.t("Please confirm it in Telegram"),
                        icon: "info",
                        showConfirmButton: false,
                        showCancelButton: true,
                        allowOutsideClick: false,
                    });

                    let interval = setInterval(async () => {
                        let {data} = await axios.get(process.env.VUE_APP_TG_WALLET_API +"/tx_status?token=" + this.telegramWalletToken + "&id=" + result.data.id);

                        switch (data.status) {
                            case "executed": {
                                popup.close();
                                clearInterval(interval);
                                resolve(data.tx_hash);
                                break;
                            }
                            case "rejected": {
                                popup.close();
                                clearInterval(interval);
                                Vue.swal({
                                    icon: "error",
                                    title: i18n.t("Transaction rejected"),
                                });
                                reject(i18n.t("Transaction rejected"));
                                break;
                            }
                            case "reverted": {
                                popup.close();
                                clearInterval(interval);
                                Vue.swal({
                                    icon: "error",
                                    title: i18n.t("Transaction reverted: {0}", [data.failed_reason]),
                                });
                                reject(data.failed_reason);
                                break;
                            }
                        }
                    }, 1000);
                });
            }
            default:
                throw new Error("unknown provider")
        }
    },
    isLoggedIn() {
        return this.loggedIn
    },
    login(provider) {
        switch (provider) {
            case PROVIDER_WC:
                walletConnect.createSession({chainId: 56});
                break
            case PROVIDER_MM:
                window.ethereum.request({ method: 'eth_requestAccounts' })
                    .then((accounts) => {
                        state.loggedIn = true
                        state.account = accounts[0]
                        this.currentProvider = provider
                    }).catch((err) => {
                        console.error(err)
                    });
                break
            case PROVIDER_TG:
                if (this.telegramWalletToken === "") {
                    const genRanHex = size => [...Array(size)].map(() => Math.floor(Math.random() * 16).toString(16)).join('');
                    let token = genRanHex(32);

                    let popup = Vue.swal({
                        title: i18n.t("Login using Telegram"),
                        grow: "column",
                        html: `                       
                        ${i18n.t("Scan this QR code")}: <br>
                        
                        <img src="https://api.qrserver.com/v1/create-qr-code/?color=000000&bgcolor=FFFFFF&data=${encodeURIComponent("tg://resolve?domain=pc_wallet_bot&start=" + token)}&qzone=1&margin=0&size=300x300&ecc=L">
                        
                        <br>
                        
                        ${i18n.t("OR")}
                        
                        <br>
                        <br>
                        
                        <a class="swal2-confirm swal2-styled" href='tg://resolve?domain=pc_wallet_bot&start=${token}'>${i18n.t("Open Telegram")}</a>
                        
                        <br>
                        <br>
`,
                        showCancelButton: true,
                        showConfirmButton: false,
                    });

                    let interval = setInterval(async () => {
                        try {
                            let {data} = await axios.get(process.env.VUE_APP_TG_WALLET_API + "/authorize?auth_token=" + token);
                            this.loggedIn = true
                            this.account = data.address
                            this.currentProvider = PROVIDER_TG
                            this.telegramWalletToken = data.token
                            clearInterval(interval);
                            popup.close();
                        } catch(e) {
                            console.log(e);
                        }
                    }, 1000);

                    popup.then(() => clearInterval(interval));
                } else {
                    axios.get(process.env.VUE_APP_TG_WALLET_API + "/me?token=" + this.telegramWalletToken).then((response) => {
                        state.loggedIn = true
                        state.account = response.data.address
                        state.currentProvider = provider
                    }).catch((err) => {
                        console.log("logging out")
                        console.error(err)
                        this.logout()
                    });
                }

                break
        }
    },
    logout() {
        switch (this.currentProvider) {
            case PROVIDER_WC:
                walletConnect.killSession()
                break
            case PROVIDER_MM:
                state.loggedIn = false
                state.account = ""
                break
            case PROVIDER_TG:
                state.loggedIn = false
                state.account = ""
                state.telegramWalletToken = ""
                break
        }

        this.currentProvider = ""
    }
})

export let tokenContract;

state.contract.methods.token().call().then((tokenAddress) => {
    state.token.address = tokenAddress;
    tokenContract = new state.web3.eth.Contract(tokenABI, state.token.address);
})

if (walletConnect.connected) {
    state.loggedIn = true
    state.account = walletConnect.accounts[0]
}

walletConnect.on("connect", (error, payload) => {
    if (error) {
        throw error;
    }

    // Get provided accounts and chainId
    const { accounts, chainId } = payload.params[0];
    console.log(accounts, chainId)
    state.loggedIn = true
    state.account = accounts[0]
    state.currentProvider = PROVIDER_WC
});

walletConnect.on("session_update", (error, payload) => {
    if (error) {
        throw error;
    }

    // Get updated accounts and chainId
    const { accounts, chainId } = payload.params[0];
    console.log(accounts, chainId)

    state.loggedIn = true
    state.account = accounts[0]
    state.currentProvider = PROVIDER_WC
});

walletConnect.on("disconnect", (error) => {
    if (error) {
        throw error;
    }

    state.loggedIn = false
    state.account = ""
    state.currentProvider = ""
});

let telegramWalletToken = localStorage.getItem("telegramWalletToken")
if (telegramWalletToken) {
    state.telegramWalletToken = telegramWalletToken
}

let provider = localStorage.getItem("currentProvider")
if (provider) {
    state.currentProvider = provider

    if (provider === PROVIDER_TG && state.telegramWalletToken === "") {
        // todo: show error
        console.log(1)
    } else {
        state.login(provider)
    }
}

let referrer = localStorage.getItem("referrerAddress")
if (referrer) {
    state.referrerAddress = referrer
}

watch(() => state.telegramWalletToken, () => {
    localStorage.setItem("telegramWalletToken", state.telegramWalletToken)
})

watch(() => state.currentProvider, () => {
    localStorage.setItem("currentProvider", state.currentProvider)
})

watch(() => state.referrerAddress, () => {
  localStorage.setItem("referrerAddress", state.referrerAddress)
})